html {
    height: 100%;
    overflow-x: hidden;
}

body {
    min-height: 100%;
    margin: 0;
    position: relative;
    padding-bottom: 120px;
}

a {
    border-bottom-style: none;
    outline: none;
}

code {
    background-color: #f9f2f4;
}

pre code {
    background-color: transparent;
}

footer {
    background-color: #262626;
    color: #fff;
    font-size: 0.8rem;
    position: absolute;
    bottom: 0;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
}

footer a,
footer a:visited {
    color: #fecd1b;
}

footer a:hover {
    text-decoration: underline;
}

pre {
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 9.5px;
    font-size: 0.8rem;
}

.btn.active {
    background-color: #e7e7e7;
}

.card {
    margin-bottom: 5px !important;
    overflow-x: auto;
}

.navbar-brand {
    padding: 5px;
    margin-right: 0;
}

.table {
    margin-bottom: 0;
}

.swh-web-app-link a {
    text-decoration: none;
    outline: none;
    border: none;
}

.swh-web-app-link:hover {
    background-color: #efeff2;
}

.table > thead > tr > th {
    border-top: none;
    border-bottom: 1px solid #e20026;
}

.table > tbody > tr > td {
    border-style: none;
}

.sitename .first-word,
.sitename .second-word {
    color: rgba(0, 0, 0, 0.75);
    font-weight: normal;
    font-size: 1.2rem;
}

.sitename .first-word {
    font-family: 'Alegreya Sans', sans-serif;
}

.sitename .second-word {
    font-family: 'Alegreya', serif;
}

.swh-api-doc-route-upcoming > td,
.swh-api-doc-route-upcoming > td > a {
    font-size: 90%;
}

.swh-api-doc-route-deprecated > td,
.swh-api-doc-route-deprecated > td > a {
    color: red;
}

.swh-api-doc p {
    margin-bottom: 0;
}

.swh-api-doc dt {
    text-align: right;
}

.swh-counter {
    font-size: 150%;
}

.swh-http-error {
    margin: 0 auto;
    text-align: center;
}

.swh-http-error-head {
    color: #2d353c;
    font-size: 30px;
}

.swh-http-error-code {
    bottom: 60%;
    color: #2d353c;
    font-size: 96px;
    line-height: 80px;
    margin-bottom: 10px !important;
}

.swh-http-error-desc {
    font-size: 12px;
    color: #647788;
    text-align: center;
}

.swh-http-error-desc pre {
    display: inline-block;
    text-align: left;
    max-width: 800px;
    white-space: pre-wrap;
}

.popover {
    max-width: 100%;
}

.modal {
    text-align: center;
    padding: 0 !important;
}

.modal::before {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -4px;
}

.modal-dialog {
    display: inline-block;
    text-align: left;
    vertical-align: middle;
}

.dropdown-submenu {
    position: relative;
}

.dropdown-submenu .dropdown-menu {
    top: 0;
    left: -100%;
    margin-top: -5px;
    margin-left: -2px;
}

.dropdown-item:hover,
.dropdown-item:focus {
    background-color: rgba(0, 0, 0, 0.1);
}

a.dropdown-left::before {
    content: "\F0D9";
    font-family: 'FontAwesome';
    display: block;
    width: 20px;
    height: 20px;
    float: left;
    margin-left: 0;
}

#swh-navbar-collapse {
    border-top-style: none;
    border-left-style: none;
    border-right-style: none;
    border-bottom: 5px solid;
    -o-border-image: linear-gradient(to right, rgb(226, 0, 38) 0%, rgb(254, 205, 27) 100%) 1 1 1 1;
       border-image: linear-gradient(to right, rgb(226, 0, 38) 0%, rgb(254, 205, 27) 100%) 1 1 1 1;
    width: 100%;
    padding: 5px;
    margin-bottom: 20px;
}

#back-to-top {
    display: initial;
    position: fixed;
    bottom: 30px;
    right: 30px;
    z-index: 10;
}

#back-to-top a img {
    display: block;
    width: 32px;
    height: 32px;
    background-size: 32px 32px;
    text-indent: -999px;
    overflow: hidden;
}


